<template>
  <div class="product-detail-view j-flex-1 j-relative j-overflow-hidden">
    <div class="bannerWrap j-relative">
      <div
        class="bgText j-relative j-flex j-justify-center j-items-center j-mb-12"
      >
        <div class="bgEn j-font-semibold">Development lifecycle</div>
        <div
          class="bgZh j-absolute-center j-font-semibold j-text-gray j-w-full j-text-center"
        >
          {{ item.name }}
        </div>
        <div class="bgZhLine j-absolute-center"></div>
      </div>
      <div class="bgContent">{{ item.introduction }}</div>
      <img class="icon4Position j-z-50" src="./images/icon4.png" alt="" />
    </div>
    <div v-if="item.frameworkImg" class="frameworkWrap j-relative">
      <div class="framework j-relative j-z-40">
        <div class="j-text-7xl j-text-black j-text-center j-relative j-mb-3">
          架构设计
        </div>
        <img
          class="j-w-full j-relative"
          :src="requireImg(item.frameworkImg)"
          alt=""
        />
      </div>
      <img
        class="allIcon1 j-absolute j-z-10"
        src="./images/all-icon1.png"
        alt=""
      />
    </div>
    <div
      v-if="advantageArr.length"
      class="advantageWrap j-relative j-w-full j-z-40"
    >
      <div
        class="bgText j-relative j-flex j-justify-center j-items-center j-pb-12 j-pt-14"
      >
        <div class="bgEn j-font-semibold">Development lifecycle</div>
        <div class="bgZh j-absolute-center j-font-semibold j-text-gray">
          优势
        </div>
      </div>
      <div class="bgContent">
        <template v-for="(item1, index1) of advantageArr">
          <fin-row :key="index1" :gutter="24">
            <template v-for="(item2, index2) of item1">
              <fin-col :key="index2" :span="8">
                <div class="item j-pl-8 j-mb-6">
                  <div
                    class="itemText j-h-full j-flex j-flex-col j-justify-center j-items-start j-text-black j-text-3"
                  >
                    <div class="itemTextTag j-mb-8">
                      <div class="itemTextTagZh j-text-4xl j-font-semibold">
                        {{ dealNumIndex(index1 * 3 + index2 + 1) }}
                      </div>
                      <div class="j-text-black-5 j-leading-4">
                        {{ dealNumEnIndex(index1 * 3 + index2 + 1) }}
                      </div>
                    </div>
                    <div class="j-font-medium j-w-4/6">{{ item2 }}</div>
                  </div>
                </div>
              </fin-col>
            </template>
          </fin-row>
        </template>
      </div>
      <img
        class="allIcon2 j-absolute j-z-10"
        src="./images/all-icon2.png"
        alt=""
      />
      <img
        class="allIcon3 j-absolute j-z-50"
        src="./images/all-icon3.png"
        alt=""
      />
    </div>
    <div v-if="resourceArr.length" class="resourceWrap j-relative">
      <div
        class="bgText j-relative j-flex j-justify-center j-items-center j-pb-12 j-pt-14"
      >
        <div class="bgEn j-font-semibold">Development lifecycle</div>
        <div class="bgZh j-absolute-center j-font-semibold j-text-black">
          资源
        </div>
      </div>
      <div class="bgContent j-z-40">
        <template v-for="(item1, index1) in resourceArr">
          <fin-row :key="index1" :gutter="24">
            <template v-for="(item2, index2) in item1">
              <fin-col :key="index2" :span="12">
                <div class="item j-pl-6" @click="goResouce(item2)">
                  <div
                    class="itemText j-h-full j-flex j-flex-col j-justify-center j-items-start j-text-black j-text-3"
                  >
                    <!-- <img
                      class="itemIcon1"
                      src="./images/resource-icon1.png"
                      alt=""
                    /> -->
                    <div class="j-flex j-items-center j-justify-start j-mt-24">
                      <div
                        class="itemTextTagZh j-text-3xl j-font-medium j-mr-5"
                      >
                        {{ item2.name }}
                      </div>
                      <img
                        class="itemIcon2"
                        src="./images/resource-icon2.png"
                        alt=""
                      />
                    </div>
                  </div>
                </div>
              </fin-col>
            </template>
          </fin-row>
        </template>
      </div>
      <img
        class="allIcon4 j-absolute j-z-50"
        src="./images/all-icon4.png"
        alt=""
      />
    </div>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';
import productJson from '@/assets/product/index.json';

export default defineComponent({
  setup(props, context) {
    const id = Number(context.root.$route.params.id);
    const item = productJson.data.find((item) => item.id === id) || {};
    const advantageArr =
      item.advantage?.reduce((old, now) => {
        if (now.type === props.active) {
          if (old[old.length - 1]?.length < 3) old[old.length - 1].push(now);
          else old.push([now]);
        }
        return old;
      }, []) || [];
    const resourceArr =
      item.resource?.reduce((old, now) => {
        if (now.type === props.active) {
          if (old[old.length - 1]?.length < 2) old[old.length - 1].push(now);
          else old.push([now]);
        }
        return old;
      }, []) || [];

    const requireImg = (url) => {
      return require(`@/assets/product/${url.replace('./', '')}`);
    };
    const dealNumIndex = (index) => {
      if (index < 10) return `0${index}`;
      else return index;
    };
    const dealNumEnIndex = (index) => {
      const en = {
        1: 'one',
        2: 'two',
        3: 'three',
        4: 'four',
        5: 'five',
        6: 'six',
      };
      return en[index] || '';
    };

    const goResouce = (item) => {
      if (item.address) window.open(item.address);
      else context.root.$message.error('暂无地址');
    };
    return {
      item,
      advantageArr,
      resourceArr,
      requireImg,
      dealNumIndex,
      dealNumEnIndex,
      goResouce,
    };
  },
});
</script>

<style lang="less">
.product-detail-view {
  input {
    background: none;
    outline: none;
    border: none;
  }
  .bannerWrap {
    background-color: #191f3a;
    background-image: url('./images/bg-detail.png');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    padding: 120px 0 160px;
    @apply j-w-full j-flex j-justify-center j-items-center j-flex-col;
    .bgZh {
      font-size: 48px;
    }
    .bgEn {
      font-size: 90px;
      color: rgba(255, 255, 255, 0.06);
    }

    .bgZhLine {
      width: 40px;
      height: 4px;
      border-radius: 3px;
      background: #fff;
      top: 100%;
    }

    .bgContent {
      width: 62.5%;
      font-size: 18px;
      @apply j-text-gray j-text-center;
      text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
    }

    .icon4Position {
      position: absolute;
      top: 54%;
      left: 3%;
      width: 243px;
      height: 313px;
    }
  }
  .frameworkWrap {
    box-shadow: 0 2px 78px 0 rgba(36, 58, 101, 0.1);
    width: 75%;
    max-width: 1400px;
    min-width: 800px;
    margin: 50px auto 70px;
  }
  .framework {
    padding: 64px 100px 50px;
    background: #fff;
    width: 100%;
    height: 100%;
  }
  .allIcon1 {
    width: 368px;
    height: 368px;
    bottom: -250px;
    left: -140px;
  }

  .advantageWrap {
    padding-bottom: 93px;
    background: url('./images/bg-advantage.png') center center no-repeat;
    background-size: cover;
    @apply j-w-full;
    .bgZh {
      font-size: 48px;
    }
    .bgEn {
      font-size: 90px;
      color: rgba(255, 255, 255, 0.06);
    }

    .bgContent {
      width: 70%;
      max-width: 1400px;
      min-width: 800px;
      margin: 0 auto;
      .item {
        height: 200px;
        position: relative;
        box-shadow: 0 8px 16px 0 rgba(0, 88, 255, 0.1);
        background: #fff url('./images/advantageIcon.png') right bottom
          no-repeat;
        background-size: 200px 180px;

        .itemTextTag {
          display: flex;
          align-items: flex-end;
          justify-content: flex-start;
          font-size: 18px;
          line-height: 32px;
        }

        .itemTextTagZh {
          color: rgba(42, 135, 252, 1);
        }
      }
    }
    .allIcon2 {
      width: 206px;
      height: 352px;
      top: 0;
      right: 54px;
      transform: translate(0, -100%);
    }
    .allIcon3 {
      width: 208px;
      height: 264px;
      bottom: 0;
      left: 128px;
      transform: translate(0, 70%);
    }
  }
  .resourceWrap {
    padding-bottom: 146px;
    @apply j-w-full;
    .bgZh {
      font-size: 48px;
    }
    .bgEn {
      font-size: 90px;
      color: rgba(62, 142, 253, 0.1);
    }

    .bgContent {
      width: 70%;
      max-width: 1400px;
      min-width: 800px;
      margin: 0 auto;
      cursor: pointer;
      .item {
        width: 517px;
        height: 243px;
        background: url('./images/bg-resource.png') center center no-repeat;
        background-size: 100% 100%;
        position: relative;
        box-shadow: 0 8px 16px 0 rgba(0, 88, 255, 0.1);
        margin: 0 auto 80px;
        transition: all 0.5s;
        transform: translateY(0);
        &:hover {
          transform: translateY(-24px);
        }
      }
      .itemIcon1 {
        width: 100px;
        height: 91px;
      }
      .itemIcon2 {
        width: 60px;
        height: 30px;
      }
    }
    .allIcon4 {
      width: 215px;
      height: 337px;
      bottom: 0;
      right: 121px;
    }
  }
}
</style>
